body {
    font-family: v.$fuente_principal;
    font-size: 1.8rem;
    min-height: 100%;
    display: flex;
    flex-direction: column;
}


a {
    text-decoration: none;
}

img {
    width: 100%;
    height: auto;
}

.swal2-popup {
    font-size: 2rem !important;
}
